* {
  margin: 0;
  padding: 0;
}

.App {
  margin: 0 auto;
  width: 100%;
  height: 100vh;
  text-align: center;
  background-color: #854d9e;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.header {
  width: 100%;
  height: 8%;
  background-color: #33233a;
  color: #fff;
  border-bottom: 2px solid #613d70;
  box-sizing: border-box;
}

.controll {
  width: 5%;
  height: 92%;
  background-color: #33233a;  
  border-right: 2px solid #613d70;
  box-sizing: border-box;
}

.field-area {
  width: 95%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}

